import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import NavBar from "./components/NavBar";
import Welcome from "./pages/Welcome";
import About from "./pages/About";
import ProductService from "./pages/ProductService";
import Deals from "./pages/Deals";
import Company from "./pages/Company";
import Contact from "./pages/Contact";
import Footer from "./components/Footer";

const App = () => {
  return (
    <div>
      <Router>
        <NavBar />
        <Switch>
          <Route exact path="/" component={Welcome}></Route>
          <Route exact path="/nosotros" component={About}></Route>
          <Route
            exact
            path="/productos-y-servicios"
            component={ProductService}
          ></Route>
          <Route
            exact
            path="/campannas-y-promociones"
            component={Deals}
          ></Route>
          <Route exact path="/para-las-empresas" component={Company}></Route>
          <Route exact path="/contactanos" component={Contact}></Route>
        </Switch>
        <Footer />
      </Router>
    </div>
  );
};

export default App;
